import React, { useContext } from "react";
import "./Style.css";
import Vector1 from "../img/Vector1.png";
import Vector2 from "../img/Vector2.png";
import boy from "../img/Sana.png";
import glassesimoji from "../img/glassesimoji.png";
import thumbup from "../img/thumbup.png";
import crown from "../img/crown.png";
import FloatinDiv from "./Floating";
import Github from "../img/github.png";
import LinkedIn from "../img/linkedin.png";

// import { themeContext } from "../../Context";
import { motion } from "framer-motion";
import { Link } from "react-scroll";
const Intro = () => {
  // Transition
  const transition = { duration: 2, type: "spring" };

  // context
//   const theme = useContext(themeContext);
//   const darkMode = theme.state.darkMode;

  return (
    <div className="Intro" id="Intro">
      {/* left name side */}
      <div className="i-left">
        <div className="i-name">
          {/* yahan change hy darkmode ka */}
          <span >Hey! I Am</span>
          <span>Sana Pawaskar</span>
          <span>
          Passionate Software Developer with 2+ years of experience crafting 
          efficient and scalable web solutions.
          </span>
        </div>
        <Link to="contact" smooth={true} spy={true}>
          <button className="button i-button">Hire me</button>
        </Link>
        {/* social icons */}
        <div className="i-icons">
         
          <a href="https://github.com/SanaPawaskar" target="_blank" rel="noopener noreferrer">
          <img src={Github} alt="Example" />
      </a>
      <a href="https://www.linkedin.com/in/sana-pawaskar/" target="_blank" rel="noopener noreferrer">
        <img src={LinkedIn} alt="Example" />
      </a>
          
          
        </div>
      </div>
      {/* right image side */}
      <div className="i-right">
        <img src={Vector1} className="size-img" alt="" />
        <img src={Vector2} className="size-img" alt="" />
        <img className="sana-pic" src={boy} alt="" />
        {/* animation */}
        <motion.img
          initial={{ left: "-36%" }}
          whileInView={{ left: "-24%" }}
          transition={transition}
          src={glassesimoji}
          alt=""
        />
 <motion.div
          initial={{ top: "-4%", left: "74%" }}
          whileInView={{ left: "68%" }}
          transition={transition}
          className="floating-div" >
  <FloatinDiv img={crown} text1="Web" text2="Developer" />
 </motion.div>
 {/* animation */}
        <motion.div
          initial={{ left: "9rem", top: "18rem" }}
          whileInView={{ left: "0rem" }}
          transition={transition}
          className="floating-div" >
           <FloatinDiv img={thumbup} text1="Full Stack" text2="Developer" />
</motion.div>
  <div className="blur" style={{ background: "rgb(238 210 255)" }}></div>
 <div
  className="blur"
  style={{  background: "#C1F5FF", top: "17rem", width: "21rem",
     height: "11rem", left: "-9rem",}}>
        </div>
      </div>
    </div>
  );
};

export default Intro;